import React, { useState, useEffect } from 'react';
import './Clock.css';

export default {
    title: 'Clock ',

}

export const Clock = function Clock() {
    const [hourDegrees, setHourDegrees] = useState(0);
    const [minuteDegrees, setMinuteDegrees] = useState(0);
    const [secondDegrees, setSecondDegrees] = useState(0);

    useEffect(() => {
        const intervalId = setInterval(() => {
            const now = new Date();
            const hours = now.getHours();
            const minutes = now.getMinutes();
            const seconds = now.getSeconds();

            const hourDegrees = ((hours / 12) * 360) + ((minutes / 60) * 30) + 90;
            const minuteDegrees = ((minutes / 60) * 360) + ((seconds / 60) * 6) + 90;
            const secondDegrees = ((seconds / 60) * 360) + 90;

            setHourDegrees(hourDegrees);
            setMinuteDegrees(minuteDegrees);
            setSecondDegrees(secondDegrees);
        }, 1000);
        return () => clearInterval(intervalId);
    }, []);

    return (
        <div className="clock">
            <div className="face">
                <div className="hour-marks">
                    {[...Array(12)].map((_, i) => (
                        <div key={i} className="hour-mark" style={{ transform: `rotate(${i * 30}deg)` }} />
                    ))}
                </div>
                <div className="minute-marks">
                    {[...Array(60)].map((_, i) => (
                        <div key={i} className="minute-mark" style={{ transform: `rotate(${i * 6}deg)` }} />
                    ))}
                </div>
                <div className="hour-hand" style={{ transform: `rotate(${hourDegrees}deg)` }}></div>
                <div className="minute-hand" style={{ transform: `rotate(${minuteDegrees}deg)` }}></div>
                <div className="second-hand" style={{ transform: `rotate(${secondDegrees}deg)` }}></div>
            </div>
        </div>
    );
};








